export default {
	data() {
		return {
			time: "2020-03-28",
			number: 0,
			com: '',
			butt: '',
			phone: -1,
			num: -1,
			station: -1,
			purpose: '',
			area: [],
			areaIndex: 0,
			area_id: {},
			ImgUrl: this.ImgUrl,
			start: ""
		}
	},
	methods: {
		// 获取区域
		getArea() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getArea',
				success: (res) => {
					res.data.data.forEach(function(item){
						that.area.push(item.dict_name)
						that.area_id[item.dict_name] = item.dict_id
					})
				}
			})
		},
		
		// 获取当前日期
		getToday(){
			let now = new Date()
			let year = now.getFullYear()
			let month = now.getMonth() + 1
			let day = now.getDate()
			if (month < 10) {
				month = '0' + month
			}
			if (day < 10) {
				day = '0' + day
			}
			this.time = year + '-' + month + '-' + day
			this.start = year + '-' + month + '-' + day
		},
		
		changeTime(e) {
			this.time = e.target.value
		},
		
		areachange(e) {
			this.areaIndex = e.target.value
		},
		
		comValue(e) {
			this.com = e.target.value
		},
		
		buttValue(e) {
			this.butt = e.target.value
		},
		
		phoneValue(e) {
			this.phone = e.target.value
		},
		
		numValue(e) {
			this.num = e.target.value
		},
		
		stationValue(e) {
			this.station = e.target.value
		},
		
		purposeValue(e) {
			this.purpose = e.target.text
		},
		
		// 发送申请
		visit() {
			let baseUrl = this.WebUrl
			let that = this
			let areaId = this.area_id[this.area[this.areaIndex]]
			if (this.com === ''){
				uni.showModal({
					title: "提示",
					content: "请输入单位名称",
					showCancel: false
				})
			}else if (this.butt === ''){
				uni.showModal({
					title: "提示",
					content: "请输入对接人名字",
					showCancel: false
				})
			}else if (!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showModal({
					title: "提示",
					content: "手机号码有误，请重新输入",
					showCancel: false
				})
			}else if (this.num <= 0 ){
				uni.showModal({
					title: "提示",
					content: "请输入参观人数",
					showCancel: false
				})
			}else if (this.station <= 0){
				uni.showModal({
					title: "提示",
					content: "请输入工作需求",
					showCancel: false
				})
			}else if (this.purpose === ''){
				uni.showModal({
					title: "提示",
					content: "请输入来访目的",
					showCancel: false
				})
			}else {
				uni.getStorage({
					key: 'u_id',
					success:function(res){
						let u_id = res.data
						
						uni.request({
							url: baseUrl + '/addVisit',
							method: 'POST',
							data: {
								area_id: areaId,
								u_id: u_id,
								visit_time: that.time,
								com_name: that.com,
								butt_joint: that.butt,
								phone: that.phone,
								num: that.num,
								station: that.station,
								purpose: that.purpose
							},
							success: (res) => {
								uni.showModal({
									title: "提示",
									content: res.data.msg,
									showCancel: false,
									success: (resd) => {
										if (resd.confirm && res.data.msg === '请求成功!') {
											uni.navigateBack({
												delta: 1
											})
										}
									}
								})
							}
						})
					}
				})
				
			}
		}
	},
	onLoad: function() {
		this.getArea()
		
		this.getToday()
	}
}
